<!--in depths--><section class="in-depths"><div class="container"><div class="content"><div class="row"><div class="col-lg-6"><div class="depths-image"><img class="img-fluid" src="@/assets/images/depths.png" alt=""></div></div><div class="col-lg-6"><div class="depths-text"><div class="main-title"><h2>DIVING IN DEPTHS</h2><p>Those who are guided by stars in the pitch dark, enormously enjoy it while unearthing priceless treasures hidden underground. They relish what Time has left for us of inscriptions which date back to roots of legacy and dawn of history as a witness to legacy whose roots run deep in history and identity. Such legacy can be traced and seen in the form of now-buried people’s laughter at the corners of houses; such smiles have been maintained on doors and windows in the form of inscriptions which you can talk with if you wish to.</p><a class="btn" href="#!">learn more</a></div></div></div></div></div></div></section><!--in depths-->